.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Movie{
  background-color:white;
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-bottom: 50px;
  text-overflow: ellipsis;
  padding: 0 20px;
  box-shadow: 0 8px 38px rgba(133,133,133,0.3), 0 5px 12px rgba(133, 133, 133, 0.22);
}
.Movie__Column{
  width: 30%;
  box-sizing: border-box;
  text-overflow: ellipsis;
}

.Movie__Column:last_child{
  padding:20px 0;
  width: 60%;
}

.Movie h1{
  font-size: 20px;
  font-weight: 600;
}

.Movie .Movie__Genres{
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.Movie__Genres .Movie__Genre{
  margin-right: 10px;
  color: #B4B5BD;
}

.Movie .Movie__Synopsis{
  text-overflow: ellipsis;
  color:#B4B5BD;
  overflow: hidden;
}

.Movie .Movie__Poster{
  max-width: 100%;
  position: relative;
  top:-20px;
  box-shadow: -10px 19px 38px rgba(83,83,83,0.3), 10px 15px 12px rgba(80, 80, 80, 0.22);
}

@media screen and (min-width:320px) and (max-width:667px){
  .Movie{
      width: 100%;
  }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
  .Movie{
      width: 100%;
      flex-direction: column;
  }

  .Movie__Poster{
      top:0;
      left: 0;
      width: 100%;
  }

  .Movie__Column{
      width: 100% !important;
  }
}